@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Muli', sans-serif;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh; 
  /* since column, align-items aligns horizontally and justify aligns vertically, for them to align vertically we must add a height */
  overflow: hidden;
  /* this hides scroll bars by default */
  background-color: #f6f7fb;
}

:root {
  --line-border-fill: #3498db;
  --line-border-empty: #e0e0e0;
}
.container {
  text-align: center;
}

.progress-container {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 30px;
  max-width: 100%;
  width: 350px;
}

.progress-container::before {
  content: '';
  background-color: #e0e0e0;
  background-color: var(--line-border-empty);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  height: 4px;
  width: 100%;
  z-index: -1; 
  /* makes sure it's behind the numbers */
}

.circle {
  background-color: white;
  color: #999;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #e0e0e0;
  border: 3px solid var(--line-border-empty);
  transition: .4s ease;
}

.circle.active {
  border-color: #3498db;
  border-color: var(--line-border-fill)
}

.btn {
  background-color: #3498db;
  background-color: var(--line-border-fill);
  color: white;
  border: 0;
  border-radius: 6px;
  cursor: pointer;
  font-family: inherit;
  padding: 8px 30px; 
  /* first is top and bottom, second left and right */
  margin: 5px;
  font-size: 14px;
}

.btn:focus {
  outline: none
}

.btn:active {
  transform: scale(0.94);
}

.btn:disabled {
  background-color: #e0e0e0;
  background-color: var(--line-border-empty);
  cursor: not-allowed;
}

.btn:hover:enabled {
  background-color: rgb(255, 119, 248);
}
